@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html, body {
    box-sizing: border-box;
    overscroll-behavior: none;
    background-color: #ebebeb;
    height: 100vh;
    width: 100vw;
  }

  *{
      margin: 0;
      padding: 0;}
  *, *:before, *:after {
      box-sizing: inherit;
  }
  
  main {
      position: fixed;
      width: 100%;
      height: 100vh;
  }
  ul {
      position: absolute;
      left: 10%;
      top: 20%;
      width: 80vw;
      height: 60%;
      display: flex;
      list-style: none;
      flex-direction: column;
      justify-content: space-between;
  }

  li {
      border-bottom: 1px solid rgba(34, 34, 34, 0.384);
      font-family: 'Playfair Display', 'Times New Roman', serif;
      font-size: 1.5rem;
      font-weight: 500;;
      line-height: 1.1;
      color: #222;
      padding: 1.5em 0;
      position: relative;
      cursor: pointer;
      transition: 1s;
      text-transform: uppercase;
  }

  li:hover {
      opacity: 1 !important;
  }